
import { post } from "../helper/util";
import { useEffect, useState } from 'react';


function Profile(){

    let [fullname, setFullname] = useState(0)
    let [email, setEmail] = useState(0)
    let [username, setUsername] = useState(0)
    let [mobile, setMobile] = useState(0)
    let [upackage, setPackage] = useState(0)
    let [expire, setExpire] = useState("")
    let [bank, setBank] = useState("")
    let [ifsc, setIfsc] = useState("")
    let [holderName, setHolderName] = useState("")

    let load = async ()=>{
        let response = await post('common/get_item',{type:'profile'});
        console.log(response.data)
        setEmail(response.data.strEmail)
        setMobile(response.data.strMobile)
        setUsername(response.data.strUserName)
        setFullname(response.data.strFirstName)

        setPackage(response.data.strPackageName)
        setExpire(response.data.strExpiry)
        setBank(response.data.strAccNo)
        setHolderName(response.data.strAccName)
        setIfsc(response.data.strIFC)

        // setTotalProfit(response.data.intCreditAmount)
        // setTotalReturns(response.data.intDebitAmount)
        // setFirstLevel(response.data.intRefCount1)
        // setSecondLevel(response.data.intRefCount2)
        // setCourseLevel(response.data.arrNewLevel)
    }
    
    
    useEffect(()=>{
        load();
    },[])

    return(
        <div className="profile">
            <h3 style={{color:'black'}}>Setting</h3>
            <div className="content-top">
               
                <div className="content-card">
                    <h4 style={{color:'black','fontSize':'18px'}}>Profile</h4>
                    <div className="profile-content">
                        <label>Fullname : {fullname}</label><br/>
                        <label>Email : {email}</label><br/>
                        <label>Mobile : {mobile}</label><br/>
                        <label>Username : {username}</label><br/>
                    </div>
                </div>
            </div>
            <br/><br/>
            <div className="profile">
                <div className="content-top">
               
                    <div className="content-card">
                        <h4 style={{color:'black','fontSize':'18px'}}>Package Details</h4>
                        <div className="profile-content">
                            <label>Current Package : {upackage}</label><br/>
                            <label>Expire at : {expire}</label><br/>
                        </div>
                    </div>
                </div>
            </div>
            <br/><br/>
            <div className="profile">
                <div className="content-top">
               
                    <div className="content-card">
                        <h4 style={{color:'black','fontSize':'18px'}}>Bank Details</h4>
                        <div className="profile-content">
                            <label>Account Number : {bank}</label><br/>
                            <label>IFSC Code : {ifsc}</label><br/>
                            <label>Bank Holder Name : {holderName}</label><br/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        

    )
}

export default Profile;