
import increacse2 from '../assets/resources/icons/top/increacse2.svg'
import checkCircleSolid from '../assets/resources/icons/btm/check-circle-solid.svg'
import checkCircleRegular from '../assets/resources/icons/btm/check-circle-regular.svg'
import { useEffect, useState } from 'react';

import { post } from "../helper/util";



function Dashboard(){

    let [accountBalance, setAccountBalance] = useState(0)
    let [totalProfit, setTotalProfit] = useState(0)
    let [totalReturns, setTotalReturns] = useState(0)
    let [firstLevel, setFirstLevel] = useState(0)
    let [secondLevel, setSecondLevel] = useState(0)
    let [courseLevel, setCourseLevel] = useState([])
    let load = async ()=>{
        let response = await post('common/get_item',{type:'dash'});
        console.log(response.data)
        setAccountBalance(response.data.intTotalAmont)
        setTotalProfit(response.data.intCreditAmount)
        setTotalReturns(response.data.intDebitAmount)
        setFirstLevel(response.data.intRefCount1)
        setSecondLevel(response.data.intRefCount2)
        setCourseLevel(response.data.arrNewLevel)
    }
    
    
    useEffect(()=>{
        load();
    },[])

    return(
        <div>
        <div className="content-top">
            <div className="content-card">
            <div className="content-card-top">
                <p className="content-card-head">
                Account<br/>
                Balance
                </p>
                <p className="content-card-data">
                {/* Last year <span>14,34,567</span> */}
                </p>
            </div>
            <div className="content-card-btm content-card-top">
                <p className="content-card-amount">${accountBalance}</p>
                <img src={increacse2} alt="" className="content-card-graph"/>
            </div>
            </div>

            <div className="content-card">
            <div className="content-card-top">
                <p className="content-card-head">
                Total<br/>
                Profit
                </p>
                <p className="content-card-data">
                {/* Last year <span>14,34,567</span> */}
                </p>
            </div>
            <div className="content-card-btm content-card-top">
                <p className="content-card-amount">${totalProfit}</p>
                <img src={increacse2} alt="" className="content-card-graph" />
            </div>
            </div>

            <div className="content-card">
                <div className="content-card-top">
                <p className="content-card-head">
                    Total<br/>
                    Returnes
                </p>
                <p className="content-card-data">
                    {/* Last year <span>14,34,567</span> */}
                </p>
                </div>
                <div className="content-card-btm content-card-top">
                <p className="content-card-amount">${totalReturns}</p>
                <img src={increacse2} alt="" className="content-card-graph" />
                </div>
            </div>
        </div>
        <div className="content-btm">
            <div className="graphholder">X</div>
            <div className="reference">
                <p className="reference-head">First Level Reference</p>
                <p className="reference-value">{firstLevel}</p>
                <p className="reference-head">Second Level Reference</p>
                <p className="reference-value">{secondLevel}</p>
                {/* <p className="reference-head">Third Level Reference</p>
                <p className="reference-value">0</p> */}
            </div>
        </div>
        <div className="content-course-status">
            <div className="content-course-status-col1">
            <p className="reference-value content-course-status-head">
                Course Process
            </p>
            <div className="process-path">
                {
                    courseLevel.map((item,index)=>{
                        return(
                            <>
                                <img src={item.chrLockStatus == 'T'?checkCircleSolid:checkCircleRegular} alt="" className="nav-icon"/>
                                <div className="processline"></div>
                            </>
                        )
                    })
                }
               
               
            </div>
            </div>
            <div className="content-course-status-col2">
                {/* <button className="period">Yearly</button>
                <button className="period">Quarterly</button>
                <button className="period">Monthly</button> */}
            </div>
        </div>
    </div>
    )
}

export default Dashboard;