
import play from '../assets/resources/icons/course/play-active.svg';
import question from '../assets/resources/icons/course/download.svg';
import unlock from  '../assets/resources/icons/course/unlock.svg';
import lock from  '../assets/resources/icons/course/lock.svg';

import 'video-react/dist/video-react.css';

import {  useEffect, useState, useRef } from 'react';
import { get, post } from '../helper/util';
import { useHistory, useParams } from 'react-router';
import { Player } from 'video-react';

function CourseDetails(){

    let [course,setCourse] = useState({})
    let [levels, setLevels] = useState([])
    let [comment, setComment] = useState([])
    let [currentVideo, setCurrentVideo] = useState([])
    let [currentCourseId, setCurrentCourseId] = useState([])
    let [currentLevelId, setCurrentLevelId] = useState([])
    let [chats, setChats] = useState([])
    let [enableChatbox, setEnableChatbox] = useState(false)
    let history = useHistory()
    let urlParams = useParams()

    let videoControl  = useRef()
    
    

    let playVideo = async (type,course,level,video)=>{
        if(type === 'Q'){
            history.push('/quiz/'+course+'/'+level);
            return ;
        }
        let videoPath = "https://vedio.swissbayedutech.com/video?videoPath="+video;
        setCurrentVideo(videoPath);
        setCurrentLevelId(level)
        console.log(video,currentVideo)
        videoControl.current.load()
        //await videoControl.current.play();
    }
    let loadChatlist =  async ()=> { 
        setChats([])
        let chatres = await post('news/get_news', {
            "APItype":"COMMENT",
            "fkLevelId":currentLevelId,
            "intPerPage":100,
            "intSkip":0
        })
        setChats(chatres.data.arrList)
    }
    
    let swicthSideBox = async (box)=>{
        if(box === 'chat'){
            loadChatlist();
            setEnableChatbox(true);
        }
        else
            setEnableChatbox(false)
    }

    let  submitComment = async()=>{
        await post('news/create_news', {
          APItype: 'COMMENT',
          fkLevelId: currentLevelId,
          jsnDetails: {
            strText: comment
          }
        }).then(() => {
            loadChatlist();
            setComment("");
        })
    }

    let commentHander = (e)=>{
        setComment(e.target.value)
    }

    let load = async ()=>{
        let response = await get('course/get_course');
        setCourse(response.data)
        setLevels(response.data.arrLevels)
        setCurrentVideo("https://vedio.swissbayedutech.com/video?videoPath=" + response.data.arrLevels[0]?.arrVedios[0]?.strUrl)
        setCurrentCourseId(urlParams.id)
        setCurrentLevelId(response.data.arrLevels[0]?.arrVedios[0]?.fkLevelId)
        videoControl.current.load()
        console.log(currentCourseId,currentLevelId);
        
    }

    useEffect(()=>{
        load();
    },[])
    return(
        <div className="course-details">
            <div className="course-details-col1" >
               
                <div>
                    <div>
                        {/* <video ref={videoControl} width="650"  controls autoPlay  style={{width:'100%'}}>
                            <source src={currentVideo} type="video/mp4" />
                        </video> */}
                        <Player ref={videoControl} width="650"  controls autoPlay  style={{width:'100%'}}>
                            <source src={currentVideo} type="video/mp4" />
                        </Player>   
                    </div>
                    
                </div>
            </div>
            
            <div className="course-details-col2 ">
                <div className="player-nav-top player-nav">
                    <p className="player-heading">{course.strCourseName}</p>
                    <p className="player-author">{course.strMentorName}</p>
                    <div className="player-options">
                        <p className={(!enableChatbox?'player-options-item player-options-item-active':'player-options-item')} onClick={swicthSideBox.bind(this,'level')}> Table Of Contents </p>
                        <p className={(enableChatbox?'player-options-item player-options-item-active':'player-options-item')}  onClick={swicthSideBox.bind(this,'chat')}> Notes </p>
                    </div>
                </div>
                <div className="level-container">
                    <div style={enableChatbox?{display:'initial'}:{display:'none'}}>
                        <div className="box box-primary direct-chat direct-chat-primary">
                            <div className="box-body">
                            {
                                chats.map((item,index)=>{
                                    return(
                                        <div className="direct-chat-messages" key={index}>
                                            <div className="direct-chat-msg">
                                                <div className="direct-chat-info clearfix">
                                                    <div className="direct-chat-text">{item.jsnDetails.strText}</div>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                            </div>
                        </div>
                        <div className="box-footer">
                            <div className="input-group chat-box-text-container">
                                <input type="text" placeholder="Type Message ..." className="form-control chat-box-text"   onChange={commentHander}  value={comment} onKeyPress={(e)=> e.key === "Enter"?submitComment():'' }/>
                                    <span className="input-group-btn">
                                        <button type="submit" className="btn btn-primary btn-flat" style={{backgroundColor:'rgb(137, 156, 202)',padding:'11px 12px',cursor:'pointer'}} onClick={submitComment}>Send</button>
                                    </span>
                            </div>
                        </div>
                    </div>
                    <div style={!enableChatbox?{display:'initial'}:{display:'none'}}>
                        {
                            levels.map((item,index)=> {
                                let enablevideo = item?.chrLockStatus;
                                return(
                                    <div key={index}>
                                        <div className="player-steps-head">
                                            <div className="player-steps-icn">
                                                <img src={enablevideo == 'T'?unlock:lock} alt="" className="player-steps-icn-lock"/>
                                            </div>
                                            <div className="player-steps-text">
                                                <p className="player-steps-head-texthead">{item.strName}</p>
                                                <p className="player-steps-head-texttime">0h 5m 48s</p>
                                            </div>
                                        </div>
                                        {
                                            
                                            item.arrVedios.map((video,innerIndex)=>{
                                                if(video.type === 'C')
                                                    return false;
                                                if(!enablevideo)
                                                    return false
                                                if(enablevideo && enablevideo === 'N')
                                                    return false;

                                                return(
                                                    <div className="player-steps-subcategory" onClick={playVideo.bind(this,video.type,video.fkCourseId,video.fkLevelId,video.strUrl)} key={"inner-div-"+index+"-"+innerIndex}>
                                                        <div className="player-steps-icn player-steps-icn-play">
                                                            <img src={video.type === 'V'?play:question} alt=""  className="player-steps-icn-play" data-lid={video.fkLevelId} data-cid={video.fkCourseId} data-videourl={video.strUrl} />
                                                        </div>
                                                        <div className="player-steps-text-sub" data-lid={video.fkLevelId} data-cid={video.fkCourseId} data-videourl={video.strUrl} onClick={playVideo.bind(this,video.type,video.fkCourseId,video.fkLevelId,video.strUrl)}>
                                                            <p className="player-steps-head-texthead player-steps-head-texthead-sub"  data-lid={video.fkLevelId} data-cid={video.fkCourseId} data-videourl={video.strUrl} onClick={playVideo.bind(this,video.type,video.fkCourseId,video.fkLevelId,video.strUrl)}>{video.strVedioName} </p>
                                                            {/* <p className="player-steps-head-texttime player-steps-head-texttime-white"> 0h 6m 38s </p> */}
                                                        </div>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                );
                            })
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CourseDetails;