
import { useState } from 'react'
import style from '../assets/index.css'
import Logo from '../assets/logo-2.png'
import {post } from '../helper/util'
import { Link, useHistory } from 'react-router-dom'
import swal from 'sweetalert'
import { useEffect } from 'react'
function Signup(){

    let [fullName, setFullName] = useState("");
    let [email, setEmail] = useState("");
    let [userPackage, setUserPackage] = useState("");
    let [mobile, setMobile] = useState("");
    let [username, setUsername] = useState("");
    let [referral, setReferral] = useState("");
    let [password, setPassword] = useState("");
    let [confirmPassword, setConfirmPassword] = useState("");

    let [packagesList, setPackagesList] = useState([]);
    let history = useHistory();

    let signup = async()=>{

      if(fullName === ""){
        swal("Name Required", "", "error");
        return "";
      }

      if(email === ""){
        swal("Email Required", "", "error");
        return "";
      }

      if(mobile === ""){
        swal("Mobile Required", "", "error");
        return "";
      }

      if(referral=== ""){
        swal("Referral Required", "", "error");
        return "";
      }

      if(username === ""){
        swal("Username Required", "", "error");
        return "";
      }

  
      if(password !== confirmPassword){
        swal("Password not matches", "", "error");
        return "";
      }

        await await post('user/create_user',{
          'strReferralCode':referral,
          'strUserName':username,
          'strPassword':password,
          'strUserType':"CUSTOMER",
          'strEmail':email,
          'strMobile':mobile,
          'fkPackageId':userPackage,
          'strFirstName':fullName,
          },{Authorization:""}).then((res)=>{
            console.log('ss',res)
            swal('Successfuly Signup',"","success").then(()=>{
              window.location.href = '/'
            })
            
        })
        .catch(()=>{
          //console.log('ee',res)
        })
      }
    useEffect(()=>{
      let load = async ()=>{
        let response = await post('common/get_drop_down',{type:"package"},{Authorization:''});
        console.log(response.data.arrList);
        setPackagesList(response.data.arrList)
    }
    
    load()
    },[])

    return(
        <div className="index-page">
        <div className={style.wrapper}>
            <div id="formContent" style={{marginTop:'5vh'}}>
                <h2 className={style.active}> Sign Up </h2>
                <div className={style.first}>
                <img src={Logo} id="logo" alt="User Icon" />
                </div>
                <div>
                    <input type="text"  className={style.second} placeholder="Full Name" onChange={(e)=>(setFullName(e.target.value))} autoComplete="false" />
                    <input type="email"  className={style.second} placeholder="Email" onChange={(e)=>(setEmail(e.target.value))} autoComplete="false" />
                    <input type="text"  className={style.second} placeholder="Mobile" onChange={(e)=>(setMobile(e.target.value))} autoComplete="false" />
                    <input type="text"  className={style.second} placeholder="Referral" onChange={(e)=>(setReferral(e.target.value))} autoComplete="false" />
                    <input type="text"  className={style.second} placeholder="Username" onChange={(e)=>(setUsername(e.target.value))} autoComplete="false" />
                    <input type="password"  className={style.second} placeholder="Password" onChange={(e)=>(setPassword(e.target.value))} autoComplete="false" />
                    <input type="password"  className={style.second} placeholder="Confirm Password" onChange={(e)=>(setConfirmPassword(e.target.value))} autoComplete="false" />
                    <input type="submit" onClick={signup} className={style.fourth} value="Sign Up" />
                </div>
                <div id="formFooter">
                  <Link to="/" className="nav-a">Login</Link>
                </div>
            </div>
        </div>
        </div>
    )
}

export default Signup