import { useState,useEffect } from "react";
import swal from "sweetalert";
import { post } from "../helper/util";


function Transaction() {
    let [creditAmount, setCreditAmount] = useState(0)
    let [debitAmount, setDebitAmount] = useState(0)
    let [roe, setRoe] = useState(0)

    let [openAddAmountModal, setOpenAddAmountModal] = useState(0)
    let [openWithdrawAmountModal, setWithdrawAddAmountModal] = useState(0)

    let [accountAddAmount, setAccountAddAmount] = useState(0)
    let [accountwithdrawAmount, setAccountWithdrawAmount] = useState(0)

    
    let [balanceHostory, setBalanceHostory] = useState([]);


    async function addAmount(){
        let response = await post('trade/create_trade',{intCreditAmount:accountAddAmount},{},'https://trade.swissbayedutech.com')
        swal('Amount Credited',response.data.strMessage).then(e=>( setAccountAddAmount(0),setOpenAddAmountModal(false)))
        load();
    }

    async function withdrawAmount(){
        let response = await post('returns/create_return',{intDebitAmount:accountwithdrawAmount,type:'TRADE'},{},'https://trade.swissbayedutech.com')
        swal('Amount Debited',response.data.strMessage).then(e=>( setAccountWithdrawAmount(0),setWithdrawAddAmountModal(false)))
        load();
    }

    let modalPopUp = (modal,open)=>{
        if(modal === 'credit')
            setOpenAddAmountModal(open)
        else
            setWithdrawAddAmountModal(open)
    }
    let load = async()=>{
        let response = await post('/trade/get_trade',{},{},'https://trade.swissbayedutech.com')
        setDebitAmount(response.data.intDebitAmount)
        setCreditAmount(response.data.intTotalAmont)
        setBalanceHostory(response.data.arrList)
        setRoe(response.data.roe)
    }

    useEffect(()=>{
        load()
    },[])

    return(
        <div>
            <div className="trade-account-over-all-report">
                <div className="content-top">
                    <div className="content-card">
                        <div className="content-card-top">
                            <p className="content-card-head">Trade <br/> Balance</p>
                            <p className="content-card-data"><button className="period" onClick={modalPopUp.bind(this,'credit',true)} >Add Amount</button></p>
                        </div>
                        <div className="content-card-btm content-card-top">
                            <p className="content-card-amount">${creditAmount}</p>
                            <img src="/static/media/increacse2.d2254016.svg" alt="" className="content-card-graph"/>
                        </div>
                    </div>
                    <div className="content-card">
                        <div className="content-card-top">
                            <p className="content-card-head">Withdraw <br/> Trade Balance</p>
                            <p className="content-card-data"><button className="period"  onClick={modalPopUp.bind(this,'withdraw',true)} >Withdraw Amount</button></p>
                        </div>
                        <div className="content-card-btm content-card-top">
                            <p className="content-card-amount">${debitAmount}</p>
                            <img src="/static/media/increacse2.d2254016.svg" alt="" className="content-card-graph"/>
                        </div>
                    </div>
                    <div className="content-card">
                        <div className="content-card-top">
                            <p className="content-card-head">ROE<br/><br/> </p>
                            <p className="content-card-data"></p>
                        </div>
                        <div className="content-card-btm content-card-top">
                            <p className="content-card-amount">{roe}</p>
                            {/* <img src="/static/media/increacse2.d2254016.svg" alt="" className="content-card-graph"/> */}
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <div className="table-div">
                    <span className="report-table-title">Trade Account History</span>
                    <table className="report-table">
                        <thead>
                            <tr>
                                <th style={{width: '65px'}}>Sl No</th>
                                <th>Trade Amount</th>
                                <th>ROE</th>
                                <th>Description</th>
                                <th>DocID</th>
                                <th>Date</th>
                            </tr>
                        </thead>
                        <tbody>
                        {balanceHostory.map((item,index)=>{
                            return(
                                <tr key={index}  className={item.intCreditAmount !==0 ? 'credit-row':'debit-row'}>
                                    <td>{index+1}</td>
                                    <td class="amount">{item.intCreditAmount !==0 ? item.intCreditAmount:item.intDebitAmount}</td>
                                    <td>{item.roe}</td>
                                    <td>{item.strText}</td>
                                    <td>{item.strDocId}</td>
                                    <td>{item.createdTime}</td>
                                </tr>
                            )
                        })}
                        </tbody>
                        
                    </table>
                </div>
            </div>

            <div data-ml-modal className={openWithdrawAmountModal?'show':''}>
                <span  onClick={modalPopUp.bind(this,'withdraw',false)} className={openWithdrawAmountModal?'modal-overlay show':'modal-overlay'}></span>
                <div className="modal-dialog modal-dialog-sm">
                    <span href="" onClick={modalPopUp.bind(this,'withdraw',false)} className="modal-close">&times;</span>
                    <h5>Withdraw</h5>
                    <div className="modal-content">
                    <div className="row">
                            <div className="col-md-12">
                                <div className="form-group">
                                    <input type="text" className="form-control mobile-text" value={accountwithdrawAmount} onChange={e=>(setAccountWithdrawAmount(e.target.value))} placeholder="Amount"  />
                                </div>
                                <div className="form-group" style={{textAlign: 'center', paddingTop: '17px'}}>
                                    <button className="period" onClick={withdrawAmount} >Credit Amount</button>
                                </div>
                            </div>
                        </div>
                    
                    </div>
                </div>
            </div>

            <div data-ml-modal className={openAddAmountModal?'show':''}>
                <span  onClick={modalPopUp.bind(this,'credit',false)} className={openAddAmountModal?'modal-overlay show':'modal-overlay'}></span>
                <div className="modal-dialog modal-dialog-sm">
                    <span href="" onClick={modalPopUp.bind(this,'credit',false)} className="modal-close">&times;</span>
                    <h5>Credit Amount</h5>
                    <div className="modal-content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group">
                                    <input type="text" className="form-control mobile-text" value={accountAddAmount} onChange={e=>(setAccountAddAmount(e.target.value))} placeholder="Amount in USD"  />
                                </div>
                                <div className="form-group" style={{textAlign: 'center', paddingTop: '17px'}}>
                                    <button className="period" onClick={addAmount} >Credit Amount</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Transaction;