import axios from "axios";
import swal from 'sweetalert'



function axiosCall(method,url,data = {},customerHeader = {},baseURL='https://trade.swissbayedutech.com'){
    let header = {
            ...{'Content-Type':'application/json', Authorization:window.localStorage.getItem('user_token')},
            ...customerHeader
        }

        //Authorization:localStorage.getItem('user_token')
    return axios.request({
        baseURL,
        url,
        method,
        data,
        headers:header
    }).then(res=>{
        return res;
    }).catch(res=>{
        if(res.response && res.response.status == 401){
            window.location.href = '/';
            return ""
        }
        if(res.response){
            swal(res.response.statusText,res.response.data.errCommon[0].strMessage)}
        if(!res.response)
            swal("Error","Oops something went wrong");
        return Promise.reject(res)
    })
}

export function get(url,data = {},customerHeader = {},baseURL='https://common.swissbayedutech.com'){
    return axiosCall('GET',url,data,customerHeader,baseURL);
}

export function post(url,data = {},customerHeader = {},baseURL='https://common.swissbayedutech.com'){
    return axiosCall('POST',url,data,customerHeader,baseURL);
}

export function put(url,data = {},customerHeader = {},baseURL='https://common.swissbayedutech.com'){
    return axiosCall('PUT',url,data,customerHeader,baseURL);
}

export function del(url,data = {},customerHeader = {},baseURL='https://common.swissbayedutech.com'){
    return axiosCall('DELETE',url,data,customerHeader,baseURL);
}

export default function custom(method,url,data = {},customerHeader = {},baseURL='https://common.swissbayedutech.com'){
    return axiosCall(method,url,data,customerHeader,baseURL)
}
